import React from 'react';

import classes from './Main.module.css';

const stages = [
    {
        phase: '01',
        title: 'Проведення обстеження',
        desc: 'Наш фахівець проводить обстеження об\'єкту та формує кошторисну пропозицію. Дана послуга є безкоштовною.'
    },
    {
        phase: '02',
        title: 'Узгодження',
        desc: 'На етапі узгодження проекту ми обговорюємо дату та терміни виконання. На цьому етапі ви оплачуєте лише вартість обладнання.'
    },
    {
        phase: '03',
        title: 'Монтаж, тестування, запуск та інструктаж',
        desc: 'Виконуємо монтаж, налаштовуємо систему, тестуємо та проводимо інструктаж для користувачів. По завершенню усіх етапів, ви оплачуєте вартість виконання робіт.'
    }
]

const Main = (props) => {
    return (
        <main>
            <section className={classes.Process}>
                <div className={classes.Wrapper}>
                    <div className={classes.Header}>
                        <span>Як ми працюємо</span>
                        <h2>Замовлення послуги</h2>
                        <p>Телефонуйте за номером <a href="/#">+38 (097) 769 97 99 </a>
                        або сформуйте замовлення через наш сайт</p>
                    </div>
                    <div className={classes.Phase}>
                        {stages.map(stage =>
                            <div key={stage.phase}>
                                <div>{stage.phase}</div>
                                <h3>{stage.title}</h3>
                                <p>{stage.desc}</p>
                            </div>
                        )}
                    </div>
                </div>
            </section>
            <section className={classes.About}>
                <div className={classes.Wrapper}>
                    <span>Про нас</span>
                    <h2>Чому ми?</h2>
                </div>
            </section>
        </main>
    )
}

export default Main;
